import { Button, Typography } from '@mui/material';
import Slider from '@mui/material/Slider';
import Stack from '@mui/system/Stack';
import React, { useEffect } from 'react';
import { useBeadApi } from './BeadApi';
import { Collapsible } from './Collapsible';
import { ColorSettingsEdit } from './ColorSettingsEdit';



interface CropBox {
    left: number,
    right: number,
    top: number,
    bottom: number,
}

// TODO maximum size
function getImageData(img: HTMLImageElement, crop: CropBox) {
    const { width, height } = img;

    const sx = (crop.left * width) / 100.0;
    const ex = (crop.right * width) / 100.0;

    const sy = (crop.top * height) / 100.0;
    const ey = (crop.bottom * height) / 100.0;

    const out_width = ex - sx;
    const out_height = ey - sy;

    const canvas = document.createElement("canvas");
    canvas.width = out_width;
    canvas.height = out_height;
    const context2d = canvas.getContext?.("2d");
    if (context2d === null) {
        return;
    }

    context2d.drawImage(img, sx, sy, ex - sx, ey - sy, 0, 0, out_width, out_height);
    const imageData = context2d.getImageData(0, 0, out_width, out_height);
    return imageData;
}


function changeToMinMax(values: number | number[]) {
    const size = values as number[];
    let min = size[0];
    let max = size[1];

    if (!Array.isArray(values)) {
        return;
    }

    if (min > max) {
        min = max;
    }

    return [min, max];
}

function CropOptions(props: { box: CropBox, onChanged?: (box: CropBox) => void }) {

    const handleChangedLR = (values: number | number[]) => {
        const box = changeToMinMax(values);
        if (!box) return;
        if (!props.onChanged) return;

        props.onChanged({
            ...props.box,
            left: box[0],
            right: box[1],
        })
    };

    const handleChangedTB = (values: number | number[]) => {
        const box = changeToMinMax(values);
        if (!box) return;
        if (!props.onChanged) return;

        props.onChanged({
            ...props.box,
            bottom: box[1],
            top: box[0],
        })
    };

    return (
        <Stack className="CropEdit">
            <Typography id="input-slider" gutterBottom>
                {"Crop width"}
            </Typography>
            <Slider
                getAriaLabel={() => 'X'}
                value={[props.box.left, props.box.right]}
                onChange={(ev, numbers) => handleChangedLR(numbers)}
                valueLabelDisplay="auto"
            />
            <Typography id="input-slider" gutterBottom>
                {"Crop height"}
            </Typography>
            <Slider
                getAriaLabel={() => 'Y'}
                value={[props.box.top, props.box.bottom]}
                onChange={(ev, numbers) => handleChangedTB(numbers)}
                valueLabelDisplay="auto"
            />
        </Stack>
    )
}



function drawImage(canvas: HTMLCanvasElement, image: HTMLImageElement, crop: CropBox) {
    const context2d = canvas.getContext?.("2d");
    if (context2d === null) {
        return;
    }

    const canvas_size = Math.min(canvas.width, canvas.height);
    const image_size = Math.max(image.width, image.height);
    const scale = canvas_size / image_size;


    const width = scale * image.width;
    const height = scale * image.height;

    const sx = (crop.left * width) / 100.0;
    const ex = (crop.right * width) / 100.0;

    const sy = (crop.top * height) / 100.0;
    const ey = (crop.bottom * height) / 100.0;

    context2d.clearRect(0.0, 0.0, canvas.width, canvas.height);

    context2d.drawImage(image, 0, 0, width, height);
    //context2d.fillStyle = "rgba(0,0,0, 0.25)";
    context2d.strokeStyle = "rgb(0, 0, 0)";
    context2d.strokeRect(sx, sy, ex - sx, ey - sy);
}


function InputImage() {
    const api = useBeadApi();
    const canvasRef = React.useRef<HTMLCanvasElement>(null);
    const containerRef = React.useRef<HTMLDivElement>(null);
    const [image, setImage] = React.useState<HTMLImageElement | null>(null);
    const [cropBox, setCropBox] = React.useState<CropBox>(
        {
            left: 0.0,
            right: 100.0,
            top: 0.0,
            bottom: 100.0
        }
    );


    const resizeCanvas = () => {
        //     if (!canvasRef.current) return;
        //     if (!containerRef.current) return;
        //     if (!image) return;

        //     const scale = Math.min(containerRef.current.clientWidth, containerRef.current.clientHeight) / Math.max(image.width, image.height);
        //     console.log(containerRef.current.clientWidth, containerRef.current.clientHeight, scale, image.width, image.height);

        //     canvasRef.current.width = Math.min(image.width, image.width * scale) + 10;
        //     canvasRef.current.height = Math.min(image.width, image.height * scale) + 10;
    };

    useEffect(
        () => {
            resizeCanvas();
            if (!image) return;
            const data = getImageData(image, cropBox);
            if (data) {
                api.set_image(data);
            }
        },
        [image, cropBox]
    );

    useEffect(
        () => {
            if (!canvasRef.current || !image) return;
            drawImage(canvasRef.current, image, cropBox);
        }, [canvasRef, image, cropBox]
    )

    const handleBoxChanged = (box: CropBox) => {
        setCropBox(box);
    };

    const handleImageChanged = (image: HTMLImageElement) => {
        setImage(image);
    }

    const loadFile = (file: File) => {
        // Reset size
        setCropBox({
            left: 0.0,
            right: 100.0,
            top: 0.0,
            bottom: 100.0
        });
        const reader = new FileReader();
        reader.onloadend = ((ev) => {
            if (!reader.result) return;

            // Use dedicated image to put return image data
            const img = new Image();
            if (!(reader.result instanceof ArrayBuffer)) {
                img.onload = () => {
                    // When done loading, extract image data and call event handler
                    handleImageChanged(img);
                }

                // Put image data both in img and the displayed imageRef
                img.src = reader.result;
            }
        });

        reader.readAsDataURL(file);
    }


    const handleImageSelect = (ev: React.ChangeEvent<HTMLInputElement>) => {
        if (!ev.target.files || ev.target.files.length == 0) {
            return;
        }
        const file = ev.target.files.item(0);
        if (!file) return;
        loadFile(file);
    }

    const handlePaste = (ev: React.ClipboardEvent) => {
        if (ev.clipboardData.files.length) {
            const fileObject = ev.clipboardData.files[0];
            loadFile(fileObject);

        }
    };

    // how to?
    // const pasteImage = () => {
    //     navigator.clipboard.read().then(
    //         (items: ClipboardItems) => {
    //             items[0].getType(items[0].types[0]).then(
    //                 (blob) => {
    //                     console.log(blob);
    //                 }
    //             )

    //         }
    //     )
    // }


    return <div className='InputImage' onPaste={handlePaste}  >
        <h2>Image</h2>
        {/* <Button onClick={pasteImage} >Paste</Button> */}
        <div className='InputImageCanvasContainer' ref={containerRef} onResize={resizeCanvas} >
            <canvas ref={canvasRef}   ></canvas>
        </div>
        <input type="file" accept='image/*' onChange={handleImageSelect} />

        <CropOptions box={cropBox} onChanged={handleBoxChanged} />
        {/* <ColorSettingsEdit /> */}

    </div >
}

export default InputImage;