import * as React from 'react';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Slider from '@mui/material/Slider';
import MuiInput from '@mui/material/Input';
import Input from '@mui/material/Input';


function maybeEmit(emit?: (value: number) => void): (value: number) => void {
    if (emit) {
        return emit;
    } else {
        return (value: number) => { }
    }
}

export function IntegerSliderEdit(props: { value: number, min: number, max: number, onChange?: (value: number) => void, onChangeCommitted?: (value: number) => void }) {

    const emitOnChange = maybeEmit(props.onChange);
    const emitChangeCommit = maybeEmit(props.onChangeCommitted);

    const handleSliderChange = (event: Event, tick: number | number[]) => {
        emitOnChange(tick as number);
    };

    const handleInputChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        const value: number = Number(event.target.value);
        emitOnChange(value);
    };

    const handleChangeCommit = (event: React.SyntheticEvent | Event, value: number | number[]) => {
        emitChangeCommit(value as number);
    };


    return <Grid container spacing={2} alignItems="center">
        <Grid item xs>
            <Slider
                value={props.value}
                step={1}
                min={props.min}
                max={props.max}
                onChange={handleSliderChange}
                onChangeCommitted={handleChangeCommit}
            />
        </Grid>
        <Grid item>
            <Input
                value={props.value}
                size="small"
                onChange={handleInputChange}
                inputProps={{
                    step: 1,
                    min: props.min,
                    max: props.max,
                    type: 'number',
                }}
            />
        </Grid>
    </Grid>

}