import React from "react";
import * as bead from "bead";
import ProfileEditor from "./ProfileEdit";
import { SizeOptions } from "./SizeOptions";
import Button from '@mui/material/Button';
import { useBeadApi } from "./BeadApi";

export function Settings(props: { pattern: bead.Pattern | undefined }) {
    const [profile, setProfile] = React.useState<bead.ColorProfile>(bead.hama_midi_primaries());

    const worker = useBeadApi();
    React.useEffect(() => {
        worker.set_profile(profile)
    }, [profile]);

    let longSize = null;
    if (props.pattern) {
        longSize = Math.max(props.pattern.width(), props.pattern.height());
    }

    return <>
        <h2>Settings</h2>
        <SizeOptions longSize={longSize} />
        <Button onClick={() => { setProfile(bead.hama_midi()) }} >Hama Midi</Button>
        <Button onClick={() => { setProfile(bead.hama_midi_primaries()) }} >Hama Midi Primaries</Button>
        <ProfileEditor profile={profile} onProfileChanged={setProfile} />
    </>
}