import React from 'react';
import * as bead from "bead";
import Checkbox from '@mui/material/Checkbox';

interface ColorInfoProps {
    color: string,
    name: string,
    code: string,
    count: number | null,
    enabled: boolean,
    onEnableChanged: (code: string, enabled: boolean) => void
}

function ColorInfo(props: ColorInfoProps) {
    let count_item = null;
    if (props.count) {
        count_item = <td>{props.count}</td>
    } else {
        count_item = <td></td>
    }

    return <tr>
        <td><Checkbox
            checked={props.enabled}
            onChange={(ev) => props.onEnableChanged(props.code, !props.enabled)}
            inputProps={{ 'aria-label': 'controlled' }}
        /></td>
        <td style={{ backgroundColor: props.color }}>{props.code}</td>
        <td>{props.name}</td>
        {count_item}
    </tr>
}

interface ProfileEditProps {
    profile: bead.ColorProfile,
    counts?: (id: string) => number,
    onProfileChanged?: (profile: bead.ColorProfile) => void,
}


function ProfileEditor(props: ProfileEditProps) {

    const handleEnabledChanged = (code: string, enabled: boolean) => {
        if (props.onProfileChanged) {
            const new_profile = props.profile.set_enabled(code, enabled);
            props.onProfileChanged(new_profile);
        }
    };

    let colorList = [];
    for (let idx = 0; idx < props.profile.color_count(); idx++) {
        let color = props.profile.color_at(idx);
        let count = null;
        if (props.counts) {
            count = props.counts(color.code());
        }

        colorList.push(<ColorInfo
            key={color.code()}
            code={color.code()}
            name={color.name()}
            color={color.color()}
            count={count}
            enabled={color.enabled()}
            onEnableChanged={handleEnabledChanged} />);
    }



    return <div className="Profile">
        <b>Profile</b>
        <table>
            <tbody>
                {colorList}
            </tbody>
        </table>
    </div>

}


export default ProfileEditor;