import React, { useEffect } from 'react';
import './App.css';
import Stack from '@mui/material/Stack';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Radio from '@mui/material/Radio';
import InputLabel from '@mui/material/InputLabel';
import Input from '@mui/material/Input/Input';
import { useBeadApi } from './BeadApi';
import { IntegerSliderEdit } from './IntegerSliderEdit';
import { Grid } from '@mui/material';
import Box from '@mui/system/Box';



export function SizeOptions(props: { longSize: number | null }) {
    const [kind, setKind] = React.useState("Longest");
    const [value, setValue] = React.useState(58);

    const defaultMaxValue = (kind: string) => {
        if (kind == "LargeHamaBoards") {
            return 12;
        } else {
            return 120;
        }
    }

    const [max, setMax] = React.useState(defaultMaxValue(kind));

    const worker = useBeadApi();

    React.useEffect(() => {
        worker.set_size(kind, value);
    }, [value, kind])

    const onValueChanged = (value: number) => {
        if (value > 0 && isFinite(value)) {
            setValue(value);
        }
    }

    const handleKindChanged = (new_kind: string) => {
        if (new_kind == "LargeHamaBoards") {
            if (props.longSize) {
                console.log(props.longSize, Math.max(Math.floor(props.longSize / 29), 1));
                setValue(Math.max(Math.pow(Math.floor(props.longSize / 29), 2), 1));
            } else {
                setValue(1);
            }
        } else if (kind == "LargeHamaBoards") {
            if (props.longSize) {
                setValue(props.longSize);
            } else {
                setValue(Math.max(1, Math.floor(Math.sqrt(value) * 29)));
            }
        }
        setMax(defaultMaxValue(new_kind));
        setKind(new_kind);
    };

    const handleChangeComitted = (n: number) => {
        const maxValue = Math.max(defaultMaxValue(kind), max, Math.floor(1.5 * n));
        setMax(maxValue);
    }


    return <div className='SizeOptions'>
        <Stack>
            <b>Size</b>
            <Box sx={{ width: '50%' }}>
                <IntegerSliderEdit value={value} min={1} max={max} onChange={onValueChanged} onChangeCommitted={handleChangeComitted} />
            </Box>
            <FormControl>
                <RadioGroup row value={kind} onChange={(event, value) => { handleKindChanged(value) }}>
                    <FormControlLabel value={"Longest"} control={<Radio />} label="Beads" />
                    <FormControlLabel value={"LargeHamaBoards"} control={<Radio />} label="Large Hama Board" />
                </RadioGroup>
            </FormControl>
        </Stack>
    </div>
}
