import React, { useContext, useEffect } from 'react';
import './App.css';
import init, * as bead from "bead";
import { InputEvent, OutputEvent } from './events';
import { ColorSettings } from './model';

export class BeadApi {
    worker: Worker;

    constructor() {
        this.worker = new Worker(new URL('./worker.ts', import.meta.url));
        this.set_size("Longest", 58);

    }

    set_message_handler(handler: (ev: OutputEvent) => void) {
        this.worker.onmessage = (ev: MessageEvent<OutputEvent>) => {
            handler(ev.data);
        }
    }

    set_size(kind: string, value: number) {
        this.post_event(
            {
                name: "SetSize",
                kind: kind,
                value: value
            }
        );
    }

    set_image(image: ImageData) {
        this.post_event(
            {
                name: "SetImage",
                image: image
            }
        );
    }

    set_profile(profile: bead.ColorProfile) {
        this.post_event(
            {
                name: "SetProfile",
                profile: profile.to_json()
            }
        )
    }

    set_settings(settings: ColorSettings) {
        this.post_event(
            {
                name: "SetColorSettings",
                settings: settings
            }
        )
    }


    post_event(event: InputEvent) {
        if (window.Worker) {
            this.worker.postMessage(event);
        }

    }
}


export const BeadContext = React.createContext<BeadApi>(new BeadApi);

export function useBeadApi() {
    return useContext(BeadContext);
}