import React, { useEffect } from 'react';
import './App.css';
import init, * as bead from "bead";
import Button from '@mui/material/Button';
import Stack from '@mui/material/Stack';
import './InputImage';
import InputImage from './InputImage';
import PatternDisplay from './PatternDisplay';
import { InputEvent, OutputEvent } from './events';
import ProfileEditor from './ProfileEdit';
import { ColorSettingsEdit } from './ColorSettingsEdit';
import { Collapsible } from './Collapsible';
import { ColorSettings } from './model';
import { SizeOptions } from "./SizeOptions";
import { BeadApi, BeadContext, useBeadApi } from "./BeadApi";
import Grid from '@mui/material/Grid';
import { Settings } from './Settings';


function App() {

  const [pattern, setPattern] = React.useState<bead.Pattern | undefined>(undefined);
  const [isLoading, setIsLoading] = React.useState(true);
  const worker = useBeadApi();

  useEffect(() => {
    init().then(() => {
      bead.bead_init();
      setIsLoading(false);
      // TODO: this should really be done by Settings?
      const profile = bead.hama_midi_primaries();
      worker.set_profile(profile);
      // setProfile(profile);
    }
    );
  }, []);

  // useEffect(() => {
  //   if (profile) {
  //     worker.set_profile(profile)
  //   }
  // }, [profile]);

  worker.set_message_handler((event: OutputEvent) => {
    switch (event.name) {
      case "NewPattern":
        const new_pattern = bead.Pattern.from_json(event.pattern);
        if (new_pattern) {
          setPattern(new_pattern);
        }
        break;
      default:
      // console.log("Unhandled event ", event.name);

    }
  });

  if (isLoading) {
    return <p>Loading</p>
  }

  let color_counts = undefined;
  if (pattern) {
    color_counts = (code: string) => {
      if (pattern) {
        return pattern.color_count(code);
      } else {
        return 0;
      }
    };
  }


  return (
    <div className="App">
      <div className="header">Bead Generator</div>
      <div className="pattern">
        <PatternDisplay pattern={pattern} />
      </div>
      <div className="image">
        <InputImage />
      </div>
      <div className="settings">
        <Settings pattern={pattern} />
      </div>

    </div>
  );


}

export default App;
